* {
    box-sizing: border-box; }

@font-face {
    font-family: 'Montserrat';
    src: url('../fonts/Montserrat-Regular.ttf');
    font-weight: regular;
    font-style: normal; }

@font-face {
    font-family: 'Montserrat';
    src: url('../fonts/Montserrat-SemiBold.ttf');
    font-weight: bold;
    font-style: normal; }

html {
    font-family: 'Montserrat';
    font-weight: regular; }

$color-blu: #5290EA;
$color-oreng: #F28B64;
$color-grey: #E2E2E2;
$color-red: #c90c0c;

.container {
    position: relative;
    max-width: 1141px;
    margin: 0 auto; }

.pervay-sekciy {
    position: relative;
    min-height: 800px;
    background: #F0F0F0;
    padding-top: 16px;
    overflow: hidden;
    margin-bottom: 32px;

    &__nav {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        height: 69px;
        align-items: center; }
    &__logo {
        width: 134px; }
    &__krug-glavnay {
        position: absolute;
        top: 129px;
        right: 48px; }
    &__krug-big {
        position: absolute;
        right: -120px;
        top: 650px; } }

.vtoray-sekciy {
    &__chelovek-gitara {
        position: relative;
        display: flex;
        flex-direction: row; } }

.nav-menu {
    &__spisok {
        display: flex;
        flex-direction: row; }
    &__text-menu {
        margin-right: 64px;
        font-size: 18px;
        font-weight: bold;
        text-decoration: none;
        &:last-child {
            margin-right: 0px; }

        a {
            text-decoration: none;
            color: black; } } }

.levay-chast1 {
    position: relative;

    &__gitarist {
        position: relative;
        z-index: 4; }
    &__prymougolnik-blu {
        position: absolute;
        width: 565.95px;
        height: 220px;
        top: 150px;
        left: -50px;
        background: $color-blu;
        border-radius: 100px;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        transform: rotate(-64deg);
        z-index: 2; }
    &__prymougolnik-orang {
        position: absolute;
        width: 618.43px;
        height: 220px;
        top: 160px;
        left: -30px;
        background: $color-oreng;
        border-radius: 100px;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        transform: rotate(-50.81deg);
        z-index: 3; }
    &__prymougolnik-grey {
        position: absolute;
        width: 608.97px;
        height: 220px;
        top: 190px;
        left: 10px;
        border-radius: 100px;
        background: $color-grey;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        transform: rotate(-36.15deg);
        z-index: 1; } }
.pravay-chast {
    position: absolute;
    width: 372px;
    height: 235px;
    top: 150px;
    right: 76px;
    &__title {
        position: relative;
        margin-bottom: 5px;
        z-index: 4; }

    &__obertka-icons {
        position: relative;
        margin-bottom: 1px;
        padding-left: 10px;
        z-index: 1; }
    &__text {
        position: relative;
        font-size: 16px;
        color: #000000;
        margin-bottom: 20px;
        z-index: 2; } }

.icons {
    display: inline-flex;

    &__item {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 17px;
        width: 40px;
        height: 40px;
        border: 1px solid $color-blu;
        border-radius: 100%;
        &:last-child {
            margin-right: 0px; }
        i {
            font-size: 21px; } } }

.title {
    position: relative;
    font-weight: bold;
    font-size: 72px;
    line-height: 68px;
    margin-bottom: 40px;
    &__hr {
        position: absolute;
        width: 65px;
        height: 0px;
        border: none;
        border-top: 1px solid #000000;
        margin: 0; } }
.buttons {
    display: inline-flex;
    &__main-btn {
        margin-right: 32px;
        width: 200px;
        height: 60px;
        background: $color-oreng;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 100px;
        border: none;
        font-size: 18px;
        color: #FFFFFF; }
    &__icon-btn {
        display: inline-flex;
        align-items: center;
        img {
            margin-right: 12px; } } }



.krug {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 194px;
    height: 194px;
    border-radius: 150px;
    background: linear-gradient(143.92deg, rgba(237, 123, 80, 0.5) 14.85%, rgba(72, 138, 234, 0.5) 53.25%, rgba(44, 44, 44, 0.5) 85.74%);
    z-index: 1;
    &_big {
        width: 282px;
        height: 282px;
        border-radius: 150px; }
    &__krug-vnutri {
        position: absolute;
        width: 129.33px;
        height: 129.33px;
        background: #F0F0F0;
        border-radius: 150px;
        z-index: 1;
        &_dig {
            width: 188px;
            height: 188px;
            background: #F0F0F0;
            transform: rotate(45deg); } } }

.tretiy-sekciy {
    position: relative;
    max-height: 640px;
    overflow: hidden;
    margin-bottom: 47px; }

.kvadrats {
    position: relative;
    display: flex;
    flex-direction: row;
    margin-bottom: 25px;
    z-index: 4;

    p {
        display: flex;
        font-size: 36px;
        align-items: center;
        text-align: center;
        color: #000000;
        transform: rotate(90deg); }
    &__p2 {
        display: flex;
        align-items: center;
        justify-items: center;
        margin-left: -24px; }

    &__kvafrat1 {
        display: flex;
        width: 360px;
        height: 360px;
        margin-right: 30px;
        background: $color-red;
        border-radius: 10px;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); }

    &__kvafrat2 {
        display: flex;
        width: 360px;
        height: 360px;
        margin-right: 30px;
        background: #F28B64;
        border-radius: 10px;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); }

    &__kvafrat3 {
        display: flex;
        width: 360px;
        height: 360px;
