        border-radius: 10px;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); } }

.chetyre-ikonki {
    display: flex;
    justify-content: space-between;
    margin-left: 20px;
    margin-right: 20px;

    &__iconsi {
        display: flex;
        margin-right: 60px;

        &:last-child {
            margin-right: 0px; }
        span {
            font-size: 48px;
            font-weight: bold; }
        img {
            margin-right: 20px; } } }

